<template>
  <div>

    <br>
    <div class="container">

<nav aria-label="breadcrumb">
  <ol class="breadcrumb">
    <li class="breadcrumb-item"><a href="#">Buscote - Solicitudes:</a></li>
    <li class="breadcrumb-item"> <a class="btn btn-primary btn-sm" @click="logout" href="#">Cerrar Sesión</a></li>
  </ol>
</nav>


      <table class="table">
        <thead class="thead-dark">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Servicio/Producto</th>
            <th scope="col">Presupuesto</th>
            <th scope="col">Nombre</th>
            <th scope="col">Email</th>
            <th scope="col">Celular</th>
            <th scope="col">Fecha</th>
            <th scope="col">Estado</th>  
            <!--th scope="col">Acción</th-->
          </tr>
        </thead>
        <tbody>
                        <tr v-for="(request, index) in requests" v-bind:key="index">
                        <td>{{index+1}}</td>
                        <td>{{request.search}}</td>
                        <td>{{request.budget}}</td>
                        <td>{{request.name}}</td>
                        <td>{{request.email}}</td>
                        <td>{{request.phone}}</td>
                        <td>{{request.date}}</td>
                        <td v-if="request.status == '0'"  style="color:red">Pendiente</td>
                        <td v-else style="color:green">Enviado</td>
                        <!--td>
                          <button class="btn btn-success btn-sm" @click="EstadoCompletely(estado)" style="font-size:9px"> Sí </button>
                          <button class="btn btn-danger btn-sm" @click="EstadoPending(estado,index)" style="font-size:9px"> No </button>
                        </td-->
                    </tr>
        </tbody>
      </table>

    </div>
    
       
  </div>
</template>

<style>
.table {
  font-size: 12px;
}
</style>


<script>
import axios from 'axios';
export default {
  name: 'Request',
  data() {
    return {
      _id:'',
      search: '',
      budget: '',
      name: '',
      email:'',
      phone:'',
      status:'',
      date:'',
      requests:[],
      estadoEditar:'',
      request:{_id:'',search:'', budget:'', name:'', email:'',phone:'',status:'',date:''}
    }
  },
  created() {
    //user is not authorized
    if (localStorage.getItem('token') === null) {
      this.$router.push('/login');
    }
  },
  mounted() {
  
      axios.get('https://buscote.herokuapp.com/request/', { headers: { token: localStorage.getItem('token')}})
      .then(res => {
          this.requests = res.data;
        //this.name = res.data.user.name;
        //this.email = res.data.user.email;
      })
      .catch(err => {
        console.log(err);
        this.$router.push('login')
      })
  },
  methods: {
    logout() {
      localStorage.clear();
      this.$router.push('/login');
    },
    EstadoCompletely(item){
      //console.log(item._id);
      // Aqui se puede aplicar Promisse ALL
      axios.put(`https://immense-journey-88777.herokuapp.com/solicitud/complety/${item._id}`, item)
      .then(res => {

            axios.get('https://immense-journey-88777.herokuapp.com/solicitud/', { headers: { token: localStorage.getItem('token')}})
      .then(res => {
          this.requests = res.data;
        //this.name = res.data.user.name;
        //this.email = res.data.user.email;
      })
      .catch(err => {
        console.log(err);
        this.$router.push('login')
      })

      })
      .catch(e => {
        console.log(e);
      })
      
    },
    EstadoPending(item){

       axios.put(`https://immense-journey-88777.herokuapp.com/solicitud/pending/${item._id}`, item)
      .then(res => {
        
            axios.get('https://immense-journey-88777.herokuapp.com/solicitud/', { headers: { token: localStorage.getItem('token')}})
      .then(res => {
          this.requests = res.data;
        //this.name = res.data.user.name;
        //this.email = res.data.user.email;
      })
      .catch(err => {
        console.log(err);
        this.$router.push('login')
      })

      })
      .catch(e => {
        console.log(e);
      })
    }
  }
  }

</script>

